.App {
  background-color: #1b1b1b;
  color: white;
}

.App-link {
  color: #61dafb;
}

.small_icon_container {
  width: 60px;
}

.small_icon_container img {
  width: 100%;
  height: auto;
}

.chess_piece {
  margin: 3px;
  border: 2px solid grey;
  border-radius: 3px;
}

.hero_viewer {
  display: flex;
  flex-direction: column;
}

.selected_hero {
  display: flex;
}

.hero_species {
